<template>
	<div id="app">
		<mt-field label="" v-model="id" placeholder="ID"> </mt-field>
		<mt-field label="" v-model="pwd" type="password" placeholder=""></mt-field>
		<mt-cell :title="': ' + area">
			<mt-button @click.native="sheetVisible = true">  </mt-button>
		</mt-cell>
		<br>
		<mt-button type="primary" @click=login() size="large"></mt-button>
		<br>
		<mt-button @click=reg() size="large"></mt-button>
		<mt-actionsheet :actions="action" v-model="sheetVisible"></mt-actionsheet>
	</div>
</template>

<script>
	import { MessageBox } from 'mint-ui'
	import { ajax } from '../script/ajax.js'
	import { Toast } from 'mint-ui';
	import { config } from '../script/config.js'
	export default {
		name: 'app',
		data() {
			return {
				msg: 'qwq',
				id: "",
				pwd: "",
				area: '',
				blog_type: "China",
				sheetVisible: false,
				action: [{
					name: '',
					method: () => { this.blog_type = 'China'; this.area = '' }
				}, {
					name: '',
					method: () => { this.blog_type = 'USA'; this.area = '' }
				}, {
					name: '',
					method: () => { this.blog_type = 'Japan'; this.area = '' }
				}
				]
			}
		},
		methods: {
			login: function () {
				ajax.postLogin({
					'id': parseInt(this.id),
					'password': this.pwd.toString()
				}, this.blog_type).then((respond) => {
					console.log(respond)
					let body = respond.body
					if (body.retc == ajax.LOGFAIL) {
						MessageBox.alert('!', '')
					} else if (body.retc == ajax.OK) {
						this.$parent.jwt = body.data.jwt
						this.$parent.nick = body.data.nick
						Toast({
							message: '',
							position: 'bottom',
							duration: config.msgTime
						});
						setTimeout(() => {
							this.$router.push('/home')
						}, config.redirectTime);
					} else {
						Toast({
							message: '',
							position: 'bottom',
							duration: config.msgTime
						});
					}
				})
			},
			reg: function () {
				this.$router.push('/reg')
			}
		}
	}
</script>

<style lang="scss">
	#app {
		width: 95%;
		margin-left: 2.5%;
	}

	.btn {
		display: flex;
		justify-content: space-between;
		margin-left: 25%;
		width: 50%;
	}
</style>


