<template>
	<div id="app">
		<mt-field label="" v-model="nick" placeholder=""></mt-field>
		<mt-field label="" v-model="id" placeholder="ID"> </mt-field>
		<mt-field label="" v-model="pwd" type="password" placeholder=""></mt-field>
		<mt-cell :title="': ' + area">
			<mt-button @click.native="sheetVisible = true">  </mt-button>
		</mt-cell>
		<br>
		<mt-button type="primary" @click.native=reg size="large"></mt-button>
		<br>
		<mt-button @click.native=login size="large"></mt-button>
		<mt-actionsheet :actions="action" v-model="sheetVisible"></mt-actionsheet>
	</div>
</template>

<script>
	import { MessageBox } from 'mint-ui'
	import { ajax } from '../script/ajax.js'
	import { Toast } from 'mint-ui';
	import { config } from '../script/config.js'
	export default {
		name: 'app',
		data() {
			return {
				msg: 'Welcome to Your Vue.js App',
				pwd: "",
				nick: "",
				id: "",
				area: '',
				blog_type: "China",
				sheetVisible: false,
				action: [{
					name: '',
					method: () => { this.blog_type = 'China'; this.area = '' }
				}, {
					name: '',
					method: () => { this.blog_type = 'USA'; this.area = '' }
				}, {
					name: '',
					method: () => { this.blog_type = 'Japan'; this.area = '' }
				}
				]
			}
		},
		methods: {
			reg: function () {
				ajax.postUser({
					"nick": this.nick.toString(),
					"id": parseInt(this.id),
					"password": this.pwd.toString()
				}, this.blog_type).then(respond => {
					console.log(respond)
					let retc = respond.body.retc
					if (retc == ajax.IDCONFLICT) {
						MessageBox.alert('', '')
					} else if (retc == ajax.OK) {
						Toast({
							message: '',
							position: 'bottom',
							duration: config.msgTime
						});
						setTimeout(() => {
							this.$router.push('/login')
						}, config.redirectTime);
					} else {
						MessageBox.alert('', '')
					}
				})
			},
			login: function () {
				this.$router.push('./login')
			}
		},
	}
</script>

<style lang="scss">
	#app {
		width: 95%;
		margin-left: 2.5%;
	}

	.btn {
		display: flex;
		justify-content: space-between;
		margin-left: 25%;
		width: 50%;
	}
</style>


